<template>
  <div class="study-home">
    <h3 class="study-time">
      学习时长：<span>{{ getTime }}</span>
    </h3>
    <el-divider />
    <div class="part">
      <div class="circle"><i class="spot" /><i class="block" /></div>
      <div class="part-wrap">
        <h3 class="title">
          part1 &nbsp;&nbsp;想知道“我们”适合吗？来测测就知道了
        </h3>
        <div class="img-wrap">
          <router-link to="/study/corporate-culture/corporate_culture"
            ><img :src="imgSrc + '20200217175305529/1581933185240.png'"
          /></router-link>
          <router-link to="/study/corporate-culture/service_responsibilities"
            ><img :src="imgSrc + '20200217175416176/1581933256242.png'"
          /></router-link>
        </div>
      </div>
    </div>
    <div class="part">
      <div class="circle"><i class="spot" /><i class="block" /></div>
      <div class="part-wrap">
        <h3 class="title">
          part2 &nbsp;&nbsp;关于健康管理师的工作，你了解多少
        </h3>
        <div class="img-wrap">
          <router-link to="/study/corporate-culture/workflow"
            ><img :src="imgSrc + '20200217175521157/1581933320863.png'"
          /></router-link>
          <router-link to="/study/workflow"
            ><img :src="imgSrc + '20200217175615090/1581933374760.png'"
          /></router-link>
        </div>
      </div>
    </div>
    <div class="part">
      <div class="circle"><i class="spot" /><i class="block" /></div>
      <div class="part-wrap">
        <h3 class="title">
          part3 &nbsp;&nbsp;看看你还需要提升的空间在哪里？
        </h3>
        <div class="img-box">
          <router-link to="/study/corporate-culture/professional_knowledge"
            ><img
              :src="imgSrc + '20200217175655946/1581933415668.png'"
              class="start-img"
              alt="业务能力"
          /></router-link>
          <div class="images">
            <div class="top">
              <img
                :src="imgSrc + '20200217173255691/1581931975434.png'"
                alt="专业能力"
                @click="goToTest('专业能力')"
              />
              <img
                :src="imgSrc + '20200217175108286/1581933067993.png'"
                alt="打字速度"
                @click="goToTest('打字速度')"
              />
            </div>
            <div class="bottom">
              <div
                class="ability-test"
                style="display: inline;cursor: pointer;"
                @click="goToTest('用户问题确诊能力')"
              >
                <img
                  :src="imgSrc + '20200217173511153/1581932110845.png'"
                  alt="用户问题确诊能力"
                />
              </div>
              <img
                :src="imgSrc + '20200217173547177/1581932146838.png'"
                alt="学习总结能力"
                @click="goToTest('学习总结能力')"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  领取计划  -->
    <tip
      v-if="getPlanShow"
      :tip-show.sync="getPlanShow"
      btn-text="领取计划"
      class="center"
      :has-footer="true"
      :model-close="false"
      @btnHandle="btnGetPlanShow"
    >
      <p slot="content">恭喜通过试岗阶段，点击领取计划</p>
    </tip>
    <!--  淘汰提示  -->
    <tip
      v-if="eliminateShow"
      :tip-show.sync="eliminateShow"
      btn-text="确定"
      class="center"
      :has-footer="true"
      @btnHandle="btnEliminateShow"
    >
      <p slot="content">很遗憾未通过试岗阶段，无法再继续向下进行</p>
    </tip>
    <tip
      v-if="isRemind"
      :tip-show.sync="isRemind"
      btn-text="开始学习"
      class="center"
      :has-footer="true"
      :close-hid="true"
      :model-close="false"
      @btnHandle="btnIsRemindShow"
    >
      <p v-if="remindData.real_day - remindData.record_day > 0" slot="content">
        {{
          remindData.record_day === 1 ? '恭喜进入' : '目前处于'
        }}试岗阶段，点击开始学习进入试岗{{
          remindData.record_day !== remindData.real_day
            ? `第${remindData.record_day + 1}`
            : `最后一`
        }}天
      </p>
      <p v-if="remindData.record_day - remindData.real_day >= 0" slot="content">
        目前处于试岗阶段，试岗阶段已超时{{
          remindData.record_day - remindData.real_day
        }}天
      </p>
    </tip>
    <el-dialog
      title="提示"
      :visible.sync="isTest"
      width="30%"
      center
      top="30vh"
    >
      <p class="center">该板块只有一次测试机会，进入则开始，请确定是否开始？</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="isTest = false">取 消</el-button>
        <el-button type="primary" @click="abilityTest">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getPlan, getStudyTime } from '@/api/learningAdmin'
import { trialRemind, createRemind, getTestStatus } from '@/api/study'
import { getInfo } from '@/api/user'
import { getTime } from '@/utils/learningMethod'
import Tip from '@/components/Dialog'
export default {
  components: {
    Tip
  },
  data() {
    return {
      getPlanShow: false,
      eliminateShow: false,
      isRemind: false, // 是否提醒天数
      remindData: {},
      isTest: false,
      time: 0,
      testProject: '',
      testStatus: [],
      imgSrc:
        'https://pic1.baobaohehu.com/fd/admin_v3/5d7f5f5c1c03121b82179aee//'
    }
  },
  computed: {
    getTime() {
      return getTime({ time: this.time })
    }
  },
  async created() {
    const { data } = await getInfo()
    if (data && data.mts_user) {
      this.$store.state.user.mts_user = data.mts_user
      if (data.mts_user.stage === -1 && data.mts_user.stage_status === 'pass')
        this.getPlanShow = true
      if (
        data.mts_user.stage === -1 &&
        data.mts_user.stage_status === 'eliminate'
      )
        this.eliminateShow = true
      if (data.mts_user.stage !== -1) this.$router.push('/study/stage')
    }
    if (this.$store.state.user.mts_user.stage === -1) this.trialRemind()
    this.studyTime()
    this.getTestStatus()
  },
  methods: {
    async btnGetPlanShow() {
      const { data } = await getPlan()
      if (data.result) window.location.href = '/study/stage'
      this.getPlanShow = false
    },
    btnEliminateShow() {
      this.eliminateShow = false
    },
    async studyTime() {
      const { data, code } = await getStudyTime()
      if (code === 20000 && data.result) {
        this.time = data.time
      }
    },
    async trialRemind() {
      // 试岗提醒
      const { code, data } = await trialRemind()
      if (code === 20000 && data.result) {
        if (data.record_day !== data.real_day) {
          this.remindData = data
          const year = new Date(data.start_at).getFullYear()
          const month = new Date(data.start_at).getMonth()
          const day = new Date(data.start_at).getDate()
          const rTime = `${year}-${month}-${day}`
          const nowYear = new Date().getFullYear()
          const nowMonth = new Date().getMonth()
          const nowDay = new Date().getDate()
          const nowTime = `${nowYear}-${nowMonth}-${nowDay}`
          if (rTime !== nowTime) this.isRemind = true
        }
      }
    },
    btnIsRemindShow() {
      this.createRemind()
      this.isRemind = false
    },
    async createRemind() {
      await createRemind({
        type: 'test_job'
      })
    },
    abilityTest() {
      if (this.testProject === '打字速度')
        this.$router.push('/study/speed-of-typing')
      else this.$router.push('/study/ability-test/' + this.testProject)
      this.isTest = false
    },
    async getTestStatus() {
      const { code, data } = await getTestStatus()
      if (code === 20000 && data.result) {
        this.testStatus = data.list
      }
    },
    goToTest(title) {
      this.testProject = title
      let titleEn = ''
      if (title === '专业能力') titleEn = 'professional_ability'
      if (title === '打字速度') titleEn = 'typing_speed'
      if (title === '用户问题确诊能力') titleEn = 'problem_diagnosis_ability'
      if (title === '学习总结能力') titleEn = 'summarizing_ability'
      const isTest = this.testStatus.filter(item => {
        return item.lesson_column === titleEn
      }).length
      if (!isTest) this.isTest = true
      else
        this.$message({
          message: '您已经完成该版块测试，不需要再测试，请完成其他未完成的测试',
          type: 'error'
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.study-home {
  padding: 20px;
  .study-time {
    font-size: 16px;
    color: #17233d;
    span {
      color: #f8747f;
    }
  }
  .part {
    display: flex;
    margin-bottom: 50px;
    .circle {
      position: relative;
      margin-right: 20px;
      margin-left: 14px;
      width: 18px;
      height: 18px;
      border: 1px solid rgb(109, 166, 255);
      border-radius: 50%;
      i {
        position: absolute;
        display: inline-block;
        border: 3px solid rgb(251, 73, 81);
      }
      .spot {
        right: 0;
        bottom: 2px;
        width: 6px;
        height: 6px;
        border-radius: 50%;
      }
      .block {
        top: -2px;
        left: -14px;
        transform: rotate(45deg);
      }
    }
    .part-wrap {
      .title {
        font-size: 18px;
        color: #17233d;
        margin-bottom: 30px;
      }
      .img-wrap {
        img {
          margin-right: 10px;
          width: 425px;
          height: 150px;
        }
      }
      .img-box {
        display: flex;
        .start-img {
          margin-right: 10px;
          width: 196px;
          height: 262px;
        }
        .images {
          display: flex;
          justify-content: space-between;
          font-size: 0;
          flex-direction: column;
          img {
            margin-right: 10px;
            width: 322px;
            height: 126px;
            cursor: pointer;
          }
        }
      }
    }
  }
}
</style>
