export const getDateInfo = function(date) {
  const y = date.getFullYear()
  let m = date.getMonth() + 1
  const d = date.getDate()
  const h = date.getHours()
  const min = date.getMinutes()
  const s = date.getSeconds()
  if (m < 10) m = '0' + m
  return `${y}-${m}-${d} ${h}:${min}:${s}`
}
export const getTime = ({ start, end, time }) => {
  if (!start && !end && !time) return '0天0小时0分钟'
  if (!time) {
    time = new Date(end) - new Date(start)
  }
  const days = Math.floor(time / (24 * 3600 * 1000))
  // 计算出小时数
  const leave1 = time % (24 * 3600 * 1000) // 计算天数后剩余的毫秒数
  const hours = Math.floor(leave1 / (3600 * 1000))
  // 计算相差分钟数
  const leave2 = leave1 % (3600 * 1000) // 计算小时数后剩余的毫秒数
  const minutes = Math.ceil(leave2 / (60 * 1000))
  // 计算相差秒数
  // const leave3 = leave2 % (60 * 1000) // 计算分钟数后剩余的毫秒数
  // const seconds = Math.round(leave3 / 1000)
  return days + '天' + hours + '小时' + minutes + '分钟'
}
export const getNowFormatDate = time => {
  const date = new Date(time)
  const seperator1 = '-'
  const seperator2 = ':'
  let month = date.getMonth() + 1
  let strDate = date.getDate()
  if (month >= 1 && month <= 9) {
    month = '0' + month
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = '0' + strDate
  }
  const currentdate =
    date.getFullYear() +
    seperator1 +
    month +
    seperator1 +
    strDate +
    ' ' +
    date.getHours() +
    seperator2 +
    date.getMinutes() +
    seperator2 +
    date.getSeconds()
  if (currentdate.indexOf('NaN') !== -1) return '/'
  return currentdate
}
